* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  .App {
    width: 100%;
    height: 100vh;
    font-family: 'Segoe UI', sans-serif;
    background-image: url(./image/ground.jpg);
    background-size: 1200px 1200px;
    background-repeat: no-repeat;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
  }
  
  .App::before {
    content: "";
    position: absolute;
    inset: 0;
    background-color: rgba(225, 247, 236, 0.8); /* üíö ÎØºÌä∏ Ïò§Î≤ÑÎ†àÏù¥ */
    backdrop-filter: blur(4px);
    z-index: 0;
  }
  
  .App-header {
    width: 100%;
    height: 100%;
    z-index: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .login {
    background-color: rgba(255, 255, 255, 0.95);
    padding: 40px 30px;
    border-radius: 16px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    width: 400px;
    position: relative;
  }
  
  .loginform {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .logo {
    background-image: url(./image/logo.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    margin-bottom: 20px;
  }
  
  .text {
    font-size: 26px;
    color: #1f2937;
    margin-bottom: 30px;
    border-bottom: 2px solid #1f2937;
    padding-bottom: 10px;
    width: 100%;
    text-align: center;
  }
  
  .loginbox {
    width: 100%;
    margin-bottom: 15px;
  }
  
  .id,
  .pw {
    width: 100%;
    height: 45px;
    margin-bottom: 12px;
    padding: 0 15px;
    border: 2px solid #bae6fd;
    background-color: #f0fdf4;
    border-radius: 8px;
    font-size: 14px;
    color: #1f2937;
    transition: 0.3s ease;
  }
  
  .id:focus,
  .pw:focus {
    border-color: #2c947e;
    background-color: #ffffff;
    box-shadow: 0 0 0 2px rgba(44, 148, 126, 0.3);
    outline: none;
  }
  
  .error-message {
    font-size: 14px;
    color: #dc2626;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .linkpage {
    width: 100%;
    height: 45px;
    background-color: #2c947e;
    color: white;
    font-weight: bold;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 4px 10px rgba(44, 148, 126, 0.3);
  }
  
  .linkpage:hover {
    background-color: #1a6e60;
    transform: translateY(-2px);
  }
  
  .findbox {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-top: 20px;
  }
  
  .findbtn {
    width: 32%;
    padding: 10px;
    font-size: 13px;
    background-color: #e2f5f0;
    color: #1f2937;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .findbtn:hover {
    background-color: #2c947e;
    color: white;
  }
  
